import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ViewTypeOfHousingComponent } from './view-type-of-housing.component';

const routes: Routes = [
  {
    path: '',
    component: ViewTypeOfHousingComponent,
    data: {
      title: 'View Type of Housing'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ViewTypeOfHousingRoutingModule {}



